.vue-wp-notice .notice-dismiss {
    position: absolute;
    top: 0;
    right: 1px;
    border: none;
    margin: 0;
    padding: 9px;
    background: none;
    color: #72777c;
    cursor: pointer;
}
.vue-wp-notice .notice-dismiss:before {
